#ds-smilies-tooltip ul.ds-smilies-tabs{display:none}
#ds-smilies-tooltip .ds-smilies-container{padding:15px 15px;height:200px;margin-left:0 !important}
#ds-smilies-tooltip .ds-smilies-container li{width:auto;max-width:100px;height:40px !important;margin:0 7px}
#footer {
    background-color: #fafafa;
    color: #777;
    padding: 1.25rem 0;
}
.blog-cover {
    background-image: url('../../img/blog/defaultBg.jpg');
    background-size: cover;
    position: fixed;
    height: 100%;
    width: 300px;
}
.blog-author a {
    font-size: 24px;
    position: fixed;
    font-weight: 400;
    top: 600px;
    left: 100px;
    color: #fff;
}
div.post .cover {
    cursor: pointer;
}
.cover {
    overflow: hidden;
}
.cover img {
    margin: auto;
    width: 100%;
    margin: -60px 0;
}
.comment-editor textarea {
    box-shadow: none;
    overflow: auto;
    padding: .625rem;
    height: 8em;
    margin: 0;
    margin: 10px 0;
    resize: none;
    padding: 10px;
    font-size: 15px;
    width: 100%;
    border: 0;
    border-radius: 5px;
    border: .0625rem dashed #ddd;
}
.post-button {
    background-image: repeating-linear-gradient(-45deg, rgb(255, 255, 255), rgb(255, 255, 255) 1.125rem, transparent 1.125rem, transparent 2.25rem);
    background-color: rgb(250, 250, 250);
    border: 0;
    width: 100%;
    height: 34px;
    border: .0625rem solid #777;
    border-radius: 5px;
    font-size: 15px;
    margin-top: 10px;
    transition: background-color 0.2s ease-in-out;
}
.post-button:hover {
    background-color: rgba(26, 188, 156, 0.5);
    color: rgb(26, 188, 156);
}
#comments input {
    border: 0;
    border: .0625rem dashed #DDD;
    width: 100%;
    height: 35px;
    border-radius: 5px;
    padding: 0 10px;
    font-size: 15px;
    margin: 10px 0;
}
.comment .post-meta {
    padding: 0;
    list-style: none;
    color: #777;
    margin-bottom: 1.25rem;
    font-size: .875rem;
}
.comment .post-meta li {
    display: inline-block;
    margin-bottom: .9375rem;
    padding: 0;
}
.comment-analysis {
    margin: 30px 0;
    border-bottom: .125rem dashed #DDD;
}
.comment-analysis .post-meta li {
    display: inline-block;
    margin-bottom: .9375rem;
    padding: 0;
}
.comment p {
    font-size: .9375rem;
    line-height: 1.5em;
    margin: .5em 0;
    word-wrap: break-word;
    text-indent: 0;
    clear: none;
    box-sizing: content-box;
    font-style: inherit;
    color: #000;
}
.avatar img {
    box-shadow: 0 .0625rem .1875rem rgba(0,0,0,0.22);
    border-radius: 30px;
    width: 50px;
}
.comment li {
    list-style: none;
}
.comment .avatar {
    display: inline-block;
}
#comments .avatar {
    position: static;
    float: left;
}
.content {
    padding-left: 4em;
}
.comment-item {
    margin: 20px 0;
    border-bottom: .125rem dashed #DDD;
}
.no-comment {
    text-align: center;
    padding-bottom: 30px;
    border-bottom: .125rem dashed #DDD;
}
@keyframes committing
{
  50% {
    background-color: rgba(26, 188, 156, 0.5);
    color: rgb(26, 188, 156);
  }
  100% {
    background-color: rgb(250, 250, 250);
    color: #000;
  }
}
.committing {
    cursor: wait;
    animation: committing 1s infinite;
}
.b-container {
    margin: 0 40px;
}
.grid {
    display: flex;
}
.new-post {
    margin-top: 20px;
    width: 100%;
    padding: 5px;
    border: 1px dashed #bbb;
}
.new-post .post-option {
    padding: 10px;
    border-bottom: 1px dashed #bbb;
}
.new-post .edit, .new-post .preview {
    padding: 10px;
    height: 530px;
    width: 50%;
}
.new-post .preview {
    overflow-y: auto;
}
.new-post .edit {
    border-right: 1px dashed #bbb;
}
.new-post .title input {
    border: 0;
    font-size: 15px;
    width: 100%;
    font-family: 'Merriweather', 'Open Sans', 'Hiragino Sans GB', 'Microsoft Yahei', 'WenQuanYi Micro Hei', 'Segoe UI Emoji', 'Segoe UI Symbol', Helvetica, Arial, sans-serif;
}
.new-post .edit textarea {
    font-size: 15px;
    border: 0;
    width: 100%;
    height: 100%;
    resize: none;
    font-family: 'Merriweather', 'Open Sans', 'Hiragino Sans GB', 'Microsoft Yahei', 'WenQuanYi Micro Hei', 'Segoe UI Emoji', 'Segoe UI Symbol', Helvetica, Arial, sans-serif;
}
.new-post .toolbar i {
    margin: 0 10px;
}
.toolbar i, .toolbar a {
    cursor: pointer;
}
.toolbar .separator {
    margin: 0 5px;
    border-left: 1px solid #bbb;
}
.submit .clearfix {
    display: block;
}
.submit .btn {
    float: right;
    cursor: pointer;
    border: 1px solid #bbb;
    margin-right: 10px;
    color: #666;
    padding: 2px 10px;
    border-radius: 2px;
    font-size: 15px;
    margin-left: 5px;
}
.submit .primary {
    color: #fff;
    background-color: #1abc9c;
    border: 1px solid #1abc9c;
}
.submit .tags {
    padding: 2px 10px;
    border-radius: 2px;
    padding-left: 0;
    font-size: 15px;
    color: #777;
}
.submit .tags input {
    font-size: 14px;
    border: 0;
    border-bottom: 1px solid #bbb;
    width: 50px;
}
.submit .tags span {
    margin-right: .3125rem;
    padding: .25rem .5rem;
    border-radius: .1875rem;
    background: rgba(153,153,153,0.1);
    color: #999;
    text-decoration: none;
    font-size: .75rem;
}
.submit .tags span a {
    cursor: pointer;
}
.trick-input {
    z-index: 39;
    font-size: 80px;
    text-align: center;
    width: 100%;
    height: 120px;
    position: fixed;
    left: 0;
    opacity: .5;
    top: 300px;
    background-color: #fff;
    padding: 0;
}


