

















































































.logs {
    padding-top: 3.125rem;
}
.logs-title {
    margin-bottom: .625rem;
    padding-bottom: .3125rem;
    color: #333;
    font-size: 2.5rem;
    -webkit-transition: all .5s;
    transition: all .5s;
    font-weight: 300;
    text-align: center;
}
.logs-item {
    margin-bottom: 50px;
    list-style: none;
}
.logs-item h3 {
    margin: 0;
    padding: 0 .9375rem;
    border-left: .25rem solid #ddd;
    color: #777;
    font-size: 1.55rem;
    font-weight: 300;
}
.logs-item ol {
    margin-left: 2.25em;
}
.logs-item ol li {
    padding: .625rem;
    color: #333;
    font-style: normal;
    line-height: 1.125rem;
    font-size: 1rem;
}



